/* parallex scrolling css only */
header {
    height: 99vh;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.play {

    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    -ms-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    background: #EDECED no-repeat;
    background-size: cover;
}


.me_container {
    padding-top: 10rem;

}

.me_photo {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 10%);
    margin-top: 5rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 2rem 1.5rem 1.5rem 1.3rem; */
}

.textStyle {
    width: max-content;
    margin: 0 auto;
    text-align: center;
    position: relative;
    padding-top: 30px;
    
}

.fontSize1 {
    font-family: nimbus;
    font-size: 4rem;
    color: var(--color-primary-variant);
    opacity: 0;
    animation: effect 3s linear;
    animation-fill-mode: forwards;
}

.fontSize2 {
    font-family: msyi;
    margin-top: -55px;
    font-size: 3.5rem;
    color: var(--color-primary-variant);
    opacity: 0;
    animation: effect 3s linear;
    animation-fill-mode: forwards;
}
@keyframes effect{
    0%{
        opacity: 1;
        color: transparent;
    }30%{
        opacity: 1;
        color: rgb(61, 53, 92);
    }60%{
        opacity: 1;
        color: rgb(45, 45, 92);
    }90%{
        opacity: 1;
        color:rgb(36, 36, 94);
    }100%{
        opacity: 1;
        color:var(--color-primary-variant);
    }
    
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
/* ipad */
@media screen and (min-width:1024px) and (max-width:1366px) {
    header {
        /* was 100 */
        height: 88vh;
    }

}

@media screen and (max-width:1024px) {
    header {
        height: 88vh;
    }



}

@media screen and (max-width:600px) {
    header {
        /* was 100vh */
        height: 80vh;

    }

    .me_container {
        padding-top: 5rem;
    }
    .textStyle {
        line-height: 100px;
    }
    .fontSize1{
     font-size: 3.5rem;
    }
    .fontSize2{
        font-size: 3rem;
    }
   
    .play {
        /* full screen background video */
       position: fixed;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        background: url('../../assets/bg.jpg') no-repeat;
        
       
    }
    .play::-webkit-media-controls-play-button {
        /* hide play button on i12 */
        display: none !important;
        -webkit-appearance: none !important;
    }

   


}