.navbar{
   width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color: white;
    position: fixed;
    z-index: 1;   
}
.brand-title{
    font-size: 1.5rem;
    margin-left: 1.5rem;
    margin-top: 7px; 
    background-color: white;
}
.imageSize {
    max-Width: 37px;
    max-height: 43px;
  }


.navbar-link ul{
    margin: 0;
    padding: 0;
    display: flex;
    transform: translate(-4%,0);
    
    
    
}
.navbar-link li{
    
    list-style: none;
}
.navbar-link li a{
   text-decoration: none;
   color: black; 
   padding: 1rem;
   display: block;
}
.navbar-link li a:hover{
    color: rgb(52, 114, 172);
    transform: translateY(-10px);
}
.navbar-link li a.active{
    color: rgb(52, 114, 172);
    
}

.toggle-button{
    position: absolute;
    top: .6rem;
    right: 1.1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
}
.toggle-button .bar{
    height: 4px;
    width: 100%;
    background-color: var(--color-primary-variant);
    border-radius: 10px;
}

@media (max-width: 600px) {
    .toggle-button{
        display: flex;
        
        
    }
    .brand-title{
        margin-left: 1.1rem;
        padding-bottom: 3px;
    }
    .imageSize {
        max-Width: 30px;
        max-height: 40px;
      }
    .navbar-link{
        display: none;
        width: 100%;
    }
   
    .navbar{
        flex-direction: column;
        align-items: flex-start;
       
    }
    .navbar-link ul{
        width: 100%;
        flex-direction: column;
    }
    .navbar-link li{
        text-align: center;
    }
    .navbar-link li a {
        padding: .5rem 1rem;
    }
    .navbar-link li a:hover{
        transform: translateY(0px);
    }
    .navbar-link.show{
        display: flex;
        
    }
}