
.portfolio__group {
    
    display: grid;
    /* miley wanted */
    /* grid-template-columns: repeat(3, .1fr);
    grid-auto-rows: 27%;
    grid-row-gap: 60px; */
    grid-template-columns: repeat(auto-fit,minmax(650px,1fr));
    grid-gap: 20px;
    margin:6em;
 
   
}

.image {
    position: relative;
    /* was 600 */
    /* width: 650px; */
    max-height: 550px; 
}

.image__img {
    display: block;
    /* width:100%; */
    height: 100%;
}

.image__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    color: #ffffff;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.25s;
}

.image__overlay--blur {
    backdrop-filter: blur(5px);
}

.image__overlay>* {
    transform: translateY(20px);
    transition: transform 0.25s;
}

.image__overlay:hover {
    opacity: 1;
}

.image__overlay:hover>* {
    transform: translateY(0);
}

.image__title {
    font-size: 2em;
    font-weight: bold;
}

.image__description {
    font-size: 1.25em;
    margin-top: 0.25em;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
/* ipad */
@media screen and (min-width:1024px) and (max-width:1366px) {
    .portfolio__group {
        grid-template-columns: repeat(2, 1fr);
        margin:4rem;
        
    }
    /* .image{
        width: 98%;
