/* MEDIA QUERIES (small DEVICES) */
@media screen and (max-width:600px) {
    #portfolio{
        padding-top: 3rem;
    }
    .portfolio__group {
        grid-template-columns: 1fr;
        place-items: center;
        margin:1rem;
        
    }
    .image{
    width: 90%;
    }
}