/* fonts from google font */

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400;500&display=swap');

@font-face{
  font-family: 'linoType';
  src:  url('./font/linoType.ttf') format('truetype');
  
}
@font-face{
    font-family: 'msyi';
    src: url('./font/msyi.ttf') format('truetype');
}
@font-face{
    font-family: 'nimbus';
    src: url('./font/nimbus.ttf') format('truetype');
}
/* Every element on the page */
* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    /* create a variable for later use*/
    /* main bg */
    --color-bg: #FFFFFF;
    /* mouse hover bg */
    --color-bg-variant: #9abede;
    /* button bg */
    --color-primary: #e9f4ff;
    --color-primary-variant: #2B5175;
    --color-black: black;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    /* with of all the container class depended on it's size */
    --container-width-lg: 100%;/*default 75%*/
    --container-width-md: 86%;
    --container-width-ms: 90%;

}

html {
    /* scroll behavior for scrolling*/
    scroll-behavior: smooth;

}

/* no scrollbar */
::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    /* this is how to use css variable  */
    background: var(--color-bg);
    color: var(--color-black);
    line-height: 1.7;
    /* prevent scroll x  */
  
    
}

/* GENERAL STYLES */
.container {
    width: var(--container-width-lg);
    /* horizontally in the middle */
    margin: 0 auto;
   
   
}

h1,
h2,
h3,
h4,
h5 {
    font-family: linoType;
    font-weight: 100;
    color: rgb(100, 99, 99);
    font-size: 1.45rem;
 
}
h1{
    font-size: 2.5rem;
}

/* Almost all the components going to be wrap in the section */
section{
margin-top: 8rem;
}

/* the direct child of section */
section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}

section >h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-black);
}

/* button */
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: #99BEE0;
    color: var(--color-bg);
    border-color:  transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: #2d5074;
}

/* Every images */
img{
    display: block;
    width: 100%;
    object-fit: cover;
}
@media screen and (min-width:1024px) and (max-width:1366px){
    /* new added code to prevent overflow-x */
    html{
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        position:relative;
    }  body{
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        position:relative;
    }
    /* prevent overflow-x code end */
}
/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px){
    /* new added code to prevent overflow-x */
    html{
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        position:relative;
    }  body{
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        position:relative;
    }
    /* prevent overflow-x code end */
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* MEDIA QUERIES (small DEVICES) */
@media screen and (max-width:600px){
    .container{
        width: var(--container-width-ms);
    }


    section > h2{
        margin-bottom: 2rem;
    }
    
}