.detail__container {
    display: block;
    max-width: 100%;
    margin: 0; 
    padding: 5%;  
    
}

.overview {
    
    width: 50%;
    height: 100%;
    transform: translate(50%, 0);
    position: relative;
   
}

.Night__description {
    display: block;
    text-align: left;
    padding-top: 1rem;
    
}


.overview img {
    margin-top: 4rem;
    margin-bottom: 2rem;
    
}

.overview h4,.Night__description h3{
    color:var(--color-primary-variant);
    font-family:'Raleway' ,sans-serif;
    font-weight:bold;
    font-size:24px ;
    

}
.Night__description p{
    font-size:1.1rem;
    color: gray;
    margin: 1rem 0 1rem 0;
    line-height: 27px;
    
    
}
.Night__description small{
    font-family:'linoType';
    color: gray;
    /* letter-spacing:'2px'; */
}
.detail-navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    color: white;
    position: fixed;
    z-index: 1;
    top: 0;
    
}

.detail-navbar-link ul{
 
    margin: 0;
    padding: 0;
    display: flex;
    
}
.detail-navbar-link li{
    
    list-style: none;
}
.detail-navbar-link li .ab{
    text-decoration: none;
    color: #2B5175; 
    /* padding: 1rem; */
    margin-right: 1rem;
    display: block;
    font-size: 2rem;
 }
.imageSize2  {
    max-width: 37px;
    max-height: 42.5px;
    /* max-height: 37px; */
    margin-top: 5px;
  
    
  }


/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px) {

    /* .exitIcon {
        transform: translate(300%, 20%);
        color: #2B5175;
    } */
    
   
}

/* MEDIA QUERIES (small DEVICES) */
@media screen and (max-width:600px) {
    
    .overview {
        width: 100%;
        /* 如果手机看detail歪了就更改这些值-1%，3 */
        transform: translate(1%, 3%);
        
    }
    .overview h4, .Night__description h3{
        font-size: 18px;
    }
    
    .imageSize2{
        max-width: 30px;
        /* 40px */
        max-height: 40px;
        margin-top: -10px;
        
       
        
    }
    
    .Night__description p{
        font-size:10px;
        line-height: 20px;
    }
    .detail-navbar-link{
        width: 12%;
    }
    .overview img {
        margin: 0;
        padding: 15px;
        
    }
}