footer{
    background: white;
    width: 100%;
    position: fixed; 
    bottom: 0;
    left: 0;
    right:0;
    height: 55px;
    
}
footer a{
    color: black;
}
.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    /* margin-bottom: 4rem;  */
    /* new line added */
    margin-top: 6px;
}
.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.1rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
/* here is a problem i can't fix */
.footer__socials a:hover{
    transform: rotate(350deg);
}
.footer__copyright{
    position: absolute;
    text-align: center;
    left: 43%;
    top:50%;
    bottom: 10px;
    font-family: linoType;
    font-size: .8rem;
    color: rgb(163, 159, 159); 
     
}
@media screen and (min-width:1024px) and (max-width:1366px) {
    .footer__copyright{
        /* was 35% */
        left: 40%;
    }
}
@media screen and (max-width: 1024px){
    .footer__copyright{
        left: 35%;
    }
}
@media screen and (max-width: 600px){
    .footer__copyright{
        left: 15%;
    }
}